* {
    box-sizing: border-box;
}

.item__text {
    text-align: center;
    width: 100%;
    height: fit-content;
    background-color: bisque;
    opacity: 0.9;
    justify-content: space-around;
    
}

.container {
    display: flex;
    flex-direction: column;
    padding: 20px 20px;
  }

.countries {
    display: flex;
    padding: 20px 40px;
    gap: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.Card {
    height: 440px;
    text-decoration: none;
    color: black;
}

a {
    text-decoration: none;
    color: white;
}

.item__name {
    font-size: 2rem;
    hyphens: auto;
}